@include breakpoint(tablet) {
    #hamburger {
        display: none;
    }
    .menu-bar {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        padding-bottom: 30px;
        height: 95px;
        div {
            margin: 0 auto;
            padding-bottom: 5px;
        }
    }
    .menu {
        display: flex !important;
        margin: 0 auto;
        li {
            font-size: 1rem;
            padding: 0 5px;
            @media screen and (orientation: landscape) {
                display: block;
                width: auto;
            }
        }
    }
    .sticky {
        position: fixed;
        width: 100%;
        background: $paragraph-color;
        z-index: 2;
        top: 0;
        left: 50%;
        padding: 0 15px;
        transform: translate(-50%,0);
        height: 95px;
        max-width: 1170px;
    }
    .showcase-btn {
        margin: 10px 0;
    }
    .aside-div {
        height: 360px;
    }
    .icon-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .icon-div {
        width: 33%;
        height: 285px;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 50px;
            height: 100px;
        }
    }
    .team-section {
        div {
            text-align: left;
            &:nth-of-type(even) {
                background: #666;
            }
            &:nth-of-type(odd) {
                background: #999;
            }
            p {
                position: absolute;
                bottom: 30px;
                left: 0;
                margin: 10px;
                padding: 0 5px;
                width: auto;
            }
            figure {
                position: absolute;
                bottom: 0;
                left: 0;
                margin: 10px;
            }
        }
    }
    .statistics-section {
        .aside-div {
            clear: both;
        }
    }
    .features-desc-div {
        &:nth-child(odd) {
            padding-right: 10px;
        }
        &:nth-child(even) {
            padding-left: 10px;
        }
        width: 50%;
        text-align: left !important;
        height: 180px;
        margin-top: 10px;
    }
    .brand-logo {
        border: solid 2px #292929;
        margin: 0;
    }
    .section-header-holder {
        justify-content: flex-start;
    }
    .img-wrapper {
        position: relative;
        width: 60%;
        max-width: 550px;
        .videoWrapper {
            position: absolute;
            top: 23px;
            left: 66px;
            width: 500px;
        }
        .video-placeholder {
            height: 99%;
        }
        img {
            width: 100%;
        }
    }
    .table-wrapper {
        display: flex;
        margin: 10px auto;
        width: 100%;
        height: 433px;
        overflow: visible;
    }
    .plan-desc {
        width: 33%;
        margin: 0 2px;
    }
    .article-holder {
        height: 190px;
        align-items: baseline;
    }
    .message-content {
        label {
            padding: 0 10px;
            margin: 10px auto;
            max-width: 100%;
        }
    }
    .message-btn {
        margin: 10px auto;
    }
    .footer-nav {
        ul {
            justify-content: space-around;
        }
    }
    .footer-mail-content {
        input {
            font-size: 2.6rem;
        }
    }
}
