@include breakpoint(large-desktop) {
.aside-div {
  height: 480px;
}
    .speciality-section {

        padding: 0 15px;
    }
    .header-content {
        padding-top: 30px;
    }
    .icon-div {
        height: 300px;
    }
    .statistics-section {
        position: relative;
        height: 400px;
        div {
            text-align: left;
        }
        .aside-div {
            height: 440px;
            clear: none;
            position: absolute;
            right: 15px;
            bottom: 0;
        }
    }
    .statistic-wrapper {
        display: flex !important;
        justify-content: flex-start;
        img {
            padding-right: 15px;
        }
    }
    .team-section {
        div {
            height: 400px;
        }
    }
    .video-section {}
    .mid-section-bg {
        background: #333;
        height: 400px;
        clear: both;
    }
    .description-wrapper,
    .header-wrapper {
        padding: 0 15px;
    }
    .iframe-maxWidth {
        max-width: 100%;
    }
    .videoWrapper {
        padding: 0;
        height: 400px;
    }
    .FAQ {
        padding: 0 15px;
    }
    .brand-logo {
        height: 300px;
    }
    .mobile-design-section {
        position: relative;
        overflow: hidden;
        height: 550px;
    }
    .btn-holder {
        position: absolute;
        top: 250px;
    }
    .m-d-btn {
        &:first-child {
            margin-right: 15px;
        }
        margin: 0;
        padding: 25px;
        width: 200px;
    }
    .article-holder {
        padding: 160px 20px 80px;
        height: 350px;
        align-items: flex-end;
    }
}
