@include breakpoint(desktop) {
    html {
        font-size: 15px;
    }
    .menu-bar {
        justify-content: space-between;
        flex-direction: row;
        align-items: center;
        div {
            margin: 0;
        }
    }
    .menu {
        margin: 0;
        li {
            padding: 0 10px;
        }
    }
    .icon-div {
        height: 275px;
    }
    .team-section div {
        height: 300px;
    }
    .features-desc-div {
        padding: 0 15px !important;
        width: 33%;
    }
    .contact-section {
        div {
            flex-direction: column;
            text-align: left;
        }
    }
    .contact-info {
        div {
            width: 100% !important;
        }
    }
    .message-content {
        width: 75%;
        float: left;
    }
    .message-btn {
        margin: 10px;
    }
    .footer-mail-content {
        span {
            width: 100%;
        }
    }
    .footer-desc {
        text-align: left;
        p {
            min-width: 1px;
            width: 100%;
            margin: 0 auto;
        }
        figure {
            margin: 10px 0;
        }
    }
}
